:root{--font-sans: "Fira Code", monospace;--font-mono: "Fira Code", monospace;--tx-1: #FFFFFF;--tx-2: #EEEEEE;--bg-1: #010000;--bg-2: #002700;--bg-3: #581200;--lk-1: #00FF00;--lk-2: #00c200;--lk-tx: #000000;--ac-1: #00FF00;--ac-tx: #000000}*{margin:0;padding:0}html,input,select,button{font-family:var(--font-sans)}body{margin:0 auto;max-width:750px;padding:2rem;border-radius:6px;overflow-x:hidden;word-break:break-word;overflow-wrap:break-word;background:var(--bg-1);color:var(--tx-2);font-size:1.03rem;line-height:1.5}dl,ol,p,pre,ul{margin-bottom:1rem}header{background:var(--bg-2);border-bottom:1px solid var(--bg-3);padding:2rem 1.5rem;margin:-2rem calc(50% - 50vw) 2rem;padding-left:calc(50vw - 50%);padding-right:calc(50vw - 50%)}header h1,header h2{padding-bottom:0;border-bottom:0}header>*:first-child{margin-top:0;padding-top:0}header>*:last-child{margin-bottom:0}h1,h2{line-height:1;color:var(--tx-1);padding-top:.875rem}h1,h2{color:var(--tx-1);padding-bottom:2px;margin-bottom:8px;border-bottom:1px solid var(--bg-2)}
